/* Portfolio.css */

#portfolio h2 {
  margin: 100px 200px 0px;
}



.projects {
  display: flex;
  flex-wrap: wrap;
  flex-basis: 10%;
  justify-content: center;
}

.project {
  margin: 20px;
  text-align: center;
}

.project img {
  width: 600px;
  height: 500px;
  object-fit: cover;
}

.project-title {
  margin-top: 10px;
  font-size: 25px;
  font-weight: bold;
  color: #fff;
}

.project-links {
  margin-top: 10px;
}

.project-links a {
  margin-right: 10px;
  color: #fff;
  text-decoration: none;
}

.project-links a:hover {
  text-decoration: underline;
}


@media (max-width: 768px) {
  #portfolio h2 {
    margin: 20px 10px 0;
  }
  .projects {
    flex-direction: column; 
  }
  .project {
    margin: 10px 0; 
    flex-basis: 100%; 
  }
  .project-title {
    font-size: 20px; 
  }
}



@media (max-width: 480px) {
  
  #portfolio h2 {
    margin: 10px;
    font-size: 20px;
  }

  .projects {
    flex-direction: column; 
  }

  .project {
    margin: 10px 0;
    flex-basis: 100%; 
  }

  .project img {
    width: 100%; 
    height: auto; 
  }

  .project-title {
    font-size: 16px; 
  }

  .project-links {
    margin-top: 5px; 
  }
}
