
body {
    font-family: 'Arial', sans-serif;
    background-color: #042b54;
  }
  
  h1, h2 {
    color: #fff;
    font-size: 50px;
  }
  
  p {
    color: #fff;
  }
  
  a {
    color: #fff;
    text-decoration: none;
  }
  
  
  input[type="text"],
  input[type="email"],
  textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #fff;
    color: #555;
  }
  
  button[type="submit"] {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  
  .error {
    color: #dc3545;
    font-size: 12px;
    margin-top: 5px;
  }
  
  @font-face {
    font-family: 'CustomFont';
    src: url('../../public/Titillium_Web/TitilliumWeb-Bold.ttf') format('truetype');
   
    
   
  }
  
  body {
    font-family: 'CustomFont', sans-serif;
  }
  
  footer {
    margin-bottom: 20px;
  }


/* Media query for screens up to 768px (typical tablets and small laptops) */
@media (max-width: 768px) {
  h1, h2 {
    font-size: 36px; /* Adjust font size for smaller screens */
  }

  button[type="submit"] {
    padding: 8px 16px; /* Adjust button padding for smaller screens */
  }

  /* Add more responsive adjustments as needed */
}

/* Media query for screens up to 480px (typical smartphones) */
@media (max-width: 480px) {
  h1, h2 {
    font-size: 24px; /* Further reduce font size for smaller screens */
  }

  button[type="submit"] {
    padding: 6px 12px; /* Further adjust button padding for small screens */
  }

  /* Add more responsive adjustments as needed */
}