
#about {
  margin: 30px; 
}

#about p {
  font-size: 25px;
}

img {
  max-width: 100%;
  border-radius: 60px;
  margin: 20px 0; 
}

.about-content {
  flex: 1;
  padding-left: 20px;
}


@media (max-width: 480px) {
  #about {
    margin: 15px;
  }

  #about p {
    font-size: 18px; 
  }

  img {
    margin: 10px 0; 
  }

  .about-content {
    padding-left: 10px; 
  }
}


@media (max-width: 768px) {
  #about {
    margin: 20px; 
  }

  #about p {
    font-size: 22px;
  }

  img {
    margin: 15px 0; 
  }

  .about-content {
    padding-left: 15px; 
  }
}
